import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import first from '@/views/my/first'

Vue.use(Router)

// console.log(Hello)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: Hello
    },
    {
      path: '/first',
      name: '第一页',
      component: first
    }
  ],
})



